<template>
  <div>
    <div class="search">
      <input v-model="name_input" id="search_input" @keyup.enter="search" placeholder="请输入客户名称"/>
      <input type="button" value="查询" @click="search"/>
    </div>
    <div id="show_box" :class="show_class">
      <div class="item">
        <div class="title">
          统一客户名称
        </div>
        <div id="unify_name" class="content">
          {{unify_name}}
        </div>
      </div>
      <!-- <div class="item">
        <div class="title">
          客户名称
        </div>
        <div id="name" class="content">
          {{name}}
        </div>
      </div> -->
      <div class="item half">
        <div class="title">
          等级
        </div>
        <div id="level" class="content">
          {{level}}
        </div>
      </div>
      <div class="item half">
        <div class="title">
          标记
        </div>
        <div id="mark" class="content">
          {{mark}}
        </div>
      </div>
      <div class="item half">
        <div class="title">
          网络经理
        </div>
        <div id="N_manager" class="content">
          {{N_manager}}
        </div>
      </div>
      <div class="item half">
        <div class="title">
          网络经理电话
        </div>
        <div id="NM_phone" class="content">
          {{NM_phone}}
        </div>
      </div>
      <div class="item half">
        <div class="title">
          客户经理
        </div>
        <div id="C_manager" class="content">
          {{C_manager}}
        </div>
      </div>
      <div class="item half">
        <div class="title">
          客户经理电话
        </div>
        <div id="CM_phone" class="content">
          {{CM_phone}}
        </div>
      </div>
    </div>
    <ul id="customer_list" :class="show_class">
      <li class="list_item title_bar">
        <span class="title long">
          统一名称
        </span>
        <span class="title long">
          客户名称
        </span>
        <span class="title">
          标记
        </span>
        <span class="title">
          等级
        </span>
        <span class="title">
          网络经理
        </span>
        <span class="title">
          网络经理电话
        </span>
        <span class="title">
          客户经理
        </span>
        <span class="title">
          客户经理电话
        </span>
        <span class="title">
          来源
        </span>
        <span class="title">
          备注
        </span>
      </li>
      <li class="list_item" v-for="item in customer_list" v-bind:key="item.id">
          <span class="content long">
            {{item.unify_name}}
          </span>
          <span class="content long">
            {{item.name}}
          </span>
          <span class="content">
            {{item.mark}}
          </span>
          <span class="content">
            {{item.level}}
          </span>
          <span class="content">
            {{item.N_manager}}
          </span>
          <span class="content">
            {{item.NM_phone}}
          </span>
          <span class="content">
            {{item.C_manager}}
          </span>
          <span class="content">
            {{item.CM_phone}}
          </span>
          <span class="content">
            {{item.orgin}}
          </span>
          <span class="content">
            {{item.remark}}
          </span>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Search',
  props: {
  },
  data:function(){
    return {
      show_class:'hidden',
      name_input:'',
      unify_name:'',
      name:'',
      level:'',
      mark:'',
      N_manager:'',
      NM_phone:'',
      C_manager:'',
      CM_phone:'',
      customer_list:[]
    }
  },
  methods:{
    search:function(){
      let self = this;
      if(this.name_input == ''){
        alert("请输入客户名称!")
        return;
      }

      let data = new FormData();
      data.append('name',this.name_input);
      axios
        .post('http://r23y847803.zicp.vip/scripts/get_customer_list.php',data)
        .then(function(res){
          if(res.data.status == 'success'){
            let list =  res.data.result;
            if(list.length == 0){
              self.show_class = 'hidden';
              alert("查询不到该客户的信息!");
              return;
            }
            self.customer_list = list;
            self.show_class = "";
            let mark_list={};
            self.unify_name = '';
            self.level = '';
            self.N_manager = '';
            self.NM_phone = '';
            self.C_manager = '';
            self.CM_phone = '';
            for(let x in list){
              let record = list[x];
              if(self.unify_name == "" && record.unify_name != ""){
                self.unify_name = record.unify_name;
              }
              if(self.level == "" && record.level != ""){
                self.level = record.level;
              }
              if(self.N_manager == "" && record.N_manager != ""){
                self.N_manager = record.N_manager;
              }
              if(self.NM_phone == "" && record.NM_phone != ""){
                self.NM_phone = record.NM_phone;
              }
              if(self.C_manager == "" && record.C_manager != ""){
                self.C_manager = record.C_manager;
              }
              if(self.CM_phone == "" && record.CM_phone != ""){
                self.CM_phone = record.CM_phone;
              }
              if(record.mark.split(" ").length > 1){
                if(record.mark.split(" ")[0] == "SET" || record.mark.split(" ")[0] == "set"){
                  if(mark_list[record.mark.split(" ")[1]] == undefined){
                    mark_list[record.mark.split(" ")[1]] = 1;
                  }else{
                    mark_list[record.mark.split(" ")[1]] = mark_list[record.mark.split(" ")[1]] + 1;
                  }
                }
                if(record.mark.split(" ")[0] == "UNSET" || record.mark.split(" ")[0] == "unset"){
                  if(mark_list[record.mark.split(" ")[1]] == undefined){
                    mark_list[record.mark.split(" ")[1]] = -1;
                  }else{
                    mark_list[record.mark.split(" ")[1]] = mark_list[record.mark.split(" ")[1]] - 1;
                  }
                }
              }
            }
            self.mark = '';
            for(let mark in mark_list){
              if(mark_list[mark] > 0){
                self.mark = self.mark + mark;
              }
            }
          }else{
            alert(res.data.errMsg);
          }
        })
        .catch(function(err){
          alert(err)
        })
    }
  }
}


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body{
  background: #EAEAEA;
}
.search{
  margin-left: 5vw;
  margin-right: 5vw;
  margin-top: 3vh;
  width:90vw;
  display: flex;
  justify-content: center;
  align-content: center;
}
#search_input{
  width: 80vw;
  height: 5vh;
  line-height: 5vh;
  font-size: 3vh;
  padding-left: 1vw;
  padding-right: 1vw; 
  background: #FFFFFF;
}
#btn_search{
  height: 5vh;
  width: 5vw;
}
#show_box{
  height: fit-content;
  width:70vw;
  background: #FFFFFF;
  border-radius: 2vh;
  margin-left: 15vw;
  margin-right: 15vw;
  margin-bottom: 3vh;
  margin-top: 3vh;
  padding: 2vw;
}
.item{
 /* height: fit-content;*/
  width:67.4vw;
  margin-left: 1.3vw;
  margin-right: 1.3vw;
  margin-top: 12px;
}
.item.half{
  width:32vw;
  margin-left: 1.3vw;
  margin-right: 1.3vw;
  display: inline-block;
}
.item .title{
  height:14px;
  font-size:12px;
  color:#EA7777;
}
.item .content{
  height:fit-content;
  min-height: 48px;
  font-size:20px;
  display:flex;
  align-items: center;
  word-break: break-all;
}
#customer_list{
  list-style: none;
  margin: 0;
  padding: 0;
}
.hidden{
  display: none;
}
.list_item{
  display: flex;
  align-items: center;
  height: fit-content;
  width: 96vw;
  margin-left: 2vw;
  margin-right: 2vw;
  padding-top: 1vh;
  padding-bottom: 1vh;
  margin-bottom: 2px;
  background: #FFFFFF;
}
.list_item.title_bar{
  background: #EA7777;
}
.list_item .title{
  display: inline-flex;
