  width: 8.25vw; 
  justify-content: center;
  align-items: center;
  font-size: 1.6vh;
  color: #FFFFFF;
}
.list_item .title.long{
  width: 15vw;
}
.list_item .content{
  display: inline-flex;
  height: fit-content;
  width: 8.25vw;
  justify-content: center;
